<template>
    <div class="yff-container">
        <h1 class="yff-heading">YFF - Godkjenn</h1>
        <div>
            <form>
                <label class="input-label">
                    Fornavn: 
                    <input class="godkjenn-input" type="text" v-model="fornavn">
                </label>
                <label class="input-label">
                    Etternavn:
                    <input class="godkjenn-input" type="text" v-model="etternavn">
                </label> 
                <label class="input-label">
                    Klasse:
                    <input class="godkjenn-input" type="text" v-model="klasse">
                </label>
                <label class="input-label">
                    Skoleår:
                    <input class="godkjenn-input" type="text" v-model="year">
                </label>
                <label class="input-label">
                    Faglærer:
                    <input class="godkjenn-input" type="text" v-model="teacher">
                </label>
                <label class="input-label">
                    Skole:
                    <input class="godkjenn-input" type="text" v-model="skole">
                </label>
                <label class="input-label">
                    Utdanningsprogram:
                    <input class="godkjenn-input" type="text" v-model="utdanningsprog">
                </label>
                <label class="input-label">
                    Utskriftsdato:
                    <input class="godkjenn-input" type="text" v-model="utskriftsdato">
                </label>
            </form>
            <app-prosjekter></app-prosjekter>
        </div>
        <div class="lpm-list-container">
            <h3>Valgte læreplanmål: </h3>
            <div>
                <label
                    v-for="lpm in valgteLpms" 
                    :key="lpm.id"
                    class="lpm-list-item"
                    :class="{chosen : lpm.checked}"
                    @click="removeLpm(lpm)"
                 >
                    <span class="lpm-text">{{ lpm.maal }}</span>
                </label>
            </div>
        </div>
        <hr>
        <p class="dark-heading" @click="leggTil = !leggTil">Trykk her for å legge til flere læreplanmål </p>
        <app-lpm-valgliste v-show="leggTil"></app-lpm-valgliste>
        <hr>
        <button class="link-btn-big" @click="createPDF('f')">Forhåndsvis / print</button>
        <button class="link-btn" @click="createPDF('s')">Lagre</button>
        <div>
          <button class="link-btn-big" @click="back">Ny elev</button>
        </div>
    </div>   
</template>

<script>
import jsPDF from 'jspdf'
import { autoTable } from 'jspdf-autotable'
import { logo } from '../assets/hfk_logo.js'
import _ from 'lodash'
import LpmValgliste from '../components/LpmValgliste.vue'
import Prosjekter from '../components/Prosjekter.vue'

export default {
    data () {
        return {
            leggTil:false
        }
    },
    methods:{
        removeLpm(lpm){
           if(confirm("Er du sikker på at du vil fjerne læreplanmål?")) {
             this.$store.commit('removeLpm', lpm)
           } else return;  
        },
        createPDF (mode) {
        let bilde = logo
        let pdfName = this.fornavn + '_' + this.etternavn + '_YFF_vedlegg'
        let doc = new jsPDF()

        //variabler
        let marg = 25
        let maxWidth = 210
        let overskriftSize = 17
        let paragrafSize = 11

        // -- HEADER -- (HFK-logo + overskrifter)
        doc.addImage(bilde, 'JPEG', marg, marg, 80, 14)
        doc.setFontSize(paragrafSize + 1)
        doc.setFontType("bold")
        doc.text(165, 38, this.utskriftsdato)
        doc.setFontSize(overskriftSize)
        doc.text(60, 65, 'PROSJEKT TIL FORDJUPNING')
        doc.text(marg, 85, 'LOKAL LÆREPLAN VG1')

        // -- ELEV-TABLE -- (info om elev)
        doc.autoTable({
          body: [
            ['Eleven sitt navn', this.etternavn + ', ' + this.fornavn ],
            ['Utdanningsprogram', this.utdanningsprog ],
            ['Klasse', this.klasse ], 
            ['Skule', this.skole ],
            ['Skuleår', this.year ],
            ['Ansvarleg lærar', this.teacher ]
          ],
          startY:92,
          margin:marg,
          showHead: false,
          theme:'grid',
          styles:{ halign:'justify' },
          columnStyles: {
            0: { cellWidth: 15 , fillColor: 200, textColor:0, lineColor:0 },
            1: { cellWidth: 50 , textColor:0, lineColor:0 }
          }
        });

        // -- FØRSTE PARAGRAF --
        doc.text(marg+8, 153, '1. Føremål med prosjekt til fordjuping')

        let foremal = "Prosjekt til fordjuping skal gjere det mogeleg for elevane å prøve ut enkelte eller fleire sider av aktuelle lærefag innan relevant utdanningsprogram og få erfaring med innhald, oppgåver og arbeidsmåtar som karakteriserer dei ulike yrka innan utdanningsprogrammet.";
        let lines = doc.setFontType("normal")
          .setFontSize(11)
          .splitTextToSize(foremal, 169);
        doc.text(marg, 160, lines);

        let foremal2 = "Faget skal gje eleven opplæring i aktiv deltaking i planlegging, gjennomføring og vurdering undervegs for at eleven skal få erfaring med dokumentasjonsarbeid og trening i eigen-refleksjon. Eleven sin dokumentasjon på arbeidet undervegs kan leggast fram når ein lærekontrakt blir inngått."
        let lines2 = doc.setFontType("normal")
          .setFontSize(11)
          .splitTextToSize(foremal2, 169);
        doc.text(marg, 175, lines2);

        // -- ANDRE UNDERTITTEL --
        doc.setFontSize(17)
        doc.setFontType("bold")
        doc.text(marg+8, 205, '2. Elevens prosjekt')

        // -- PROSJEKT-TABLE -- (info om prosjekter)
        doc.autoTable({
          head:[['Prosjektnr', 'Lærefag Vg3', 'Organisering/Læringsarena', 'Årstimar']],
          body: [
            [ this.prosjektNr, this.laerefag, this.organisering, this.yearlyHours ]
          ],
          startY:209,
          margin:marg,
          theme:'grid',
          styles:{ halign:'justify', textColor:0, lineColor:0, fillColor:255 },
          headStyles:{ lineWidth:0.1, fillColor:200 }
        });

        // -- TREDJE PARAGRAF --
        doc.setFontSize(overskriftSize)
        doc.setFontType("bold")
        doc.text(marg+8, 240, '3. Kompetansemål - Grunnlag for vurdering')

        let kompetanse = "Dei nasjonale måla med lokal konkretisering dannar grunnlag for vurdering. Fleire kompetansemål og område kan sjåast i samanheng. Konkretiseringa skal tilpassast nivået og formålet med faget. Det skal gi grunnlag for dialog mellom elevar, lærarar og aktuelle samarbeidspartnarar. Eitt av kompetansemåla skal vere at eleven skal dokumentere arbeidet undervegs (rapport/logg eller liknande)."
        let lines3 = doc.setFontType("normal")
          .setFontSize(11)
          .splitTextToSize(kompetanse, 169);
        doc.text(marg, 247, lines3);

        // -- PAGE BREAK --
        doc.addPage()

        doc.setFontSize(15)
        doc.setFontType("bold")
        doc.text(20, 50, 'Prosjekt 1')

        doc.setFontSize(13)
        doc.setFontType("bold")
        doc.text(60, 50, 'Læreplan i salgsfaget Vg3')

        doc.setDrawColor(0, 0, 0);
        doc.rect(20, 60, 170, 150) 

        doc.setFontSize(15)
        doc.setFontType("bold")
        doc.text(22, 67, 'Lokale mål for opplæringa er at eleven skal kunne:')

        let valgteLpms = [...new Set(this.$store.getters.getValgteLPM.map(lpm => lpm.emne))]
        let lpmTitleStart = 85
        let lpmTitleOffset = 0
        let i = 0
        let offsetVar = 0

        valgteLpms.forEach(lpm => {

          doc.setFontSize(15)
          doc.setFontType("bold")
          doc.text(22, lpmTitleStart + lpmTitleOffset, lpm)

          let lpms_kategorisert = _.pickBy(this.valgteLpms, _.iteratee(['emne', lpm]))

          let bag = []
          
          _.forOwn(lpms_kategorisert, function(lpm) {
            
            if(bag.includes(lpm.emne)){
              i+=11
              offsetVar += 13
            }else{
              bag.push(lpm.emne)
              i+=25
              offsetVar += 23
            }

            doc.circle(25, 67 + i, 1, 'F')   
            let lpmText = lpm.maal
            let lpm_lines = doc.setFontType("normal")
              .setFontSize(11)
              .splitTextToSize(lpmText, 155)
            doc.text(30, 68 + i, lpm_lines)
          });
         
          lpmTitleOffset += offsetVar

       })

        //avhenger av lpms

        // doc.circle(25, 90, 1, 'F')


        doc.setFontSize(12)
        doc.setFontType("bold")
        doc.text(20, 235, 'Planen er gjennomgått av')

        doc.setFontSize(10)
        doc.setFontType("bold")
        doc.text(20, 250, 'Lærar:')
        doc.setFontSize(10)
        doc.setFontType("normal")
        doc.text(32, 250, this.teacher )

        doc.setFontSize(10)
        doc.setFontType("bold")
        doc.text(120, 250, 'Elev:')
        doc.setFontSize(10)
        doc.setFontType("normal")
        doc.text(130, 250, this.etternavn + ', ' + this.fornavn )

        doc.setFontSize(12)
        doc.setFontType("bold")
        doc.text(20, 265, 'Skulen si endelege godkjenning')

        doc.setFontSize(10)
        doc.setFontType("normal")
        doc.text(20, 275, 'Tertnes videregående skole')

        doc.text(20, 280, 'Dato:')
        doc.text(30, 280, this.utskriftsdato )

        doc.line(120, 281, 190, 281)
        doc.text(152, 287, 'Rektor')

        //doc.save(pdfName + '.pdf')
        if(mode === 'f'){
          doc.autoPrint()
          doc.output('dataurlnewwindow');
        } 

        if(mode === 's') {
          doc.save(pdfName + '.pdf');
        }
    },
    back(){
      this.$store.commit('velgElev', '')
      this.$store.commit('clearValgte')


      //clear vg1 admøk
       let vg1_adm_oko = this.$store.state.vg1_adm_oko_lpm

      _.findKey(vg1_adm_oko, function(lpm) {
          lpm.checked = false
      });

      //clear vg1 markedsføring
      let vg1_mrkf_salg = this.$store.state.vg1_mrkf_salg_lpm

      _.findKey(vg1_mrkf_salg, function(lpm) {
          lpm.checked = false
      });

      //clear vg1 sikkerhet og transport
      let vg1_sikk_trans = this.$store.state.vg1_sikk_trans_lpm

      _.findKey(vg1_sikk_trans, function(lpm) {
          lpm.checked = false
      });

      //clear vg2 mrkf salg
      let vg2_mrkf_salg = this.$store.state.vg2_mrkf_salg_lpm

      _.findKey(vg2_mrkf_salg, function(lpm) {
          lpm.checked = false
      });

      //clear vg2 økoadm
      let vg2_oko_adm = this.$store.state.vg2_oko_adm_lpm

      _.findKey(vg2_oko_adm, function(lpm) {
          lpm.checked = false
      });

      //clear vg2 sikkerhet
      let vg2_sikkerhet = this.$store.state.vg2_sikkerhet_lpm

      _.findKey(vg2_sikkerhet, function(lpm) {
          lpm.checked = false
      });

      //clear vg3 kontor og adm
      let vg3_kontor_adm = this.$store.state.vg3_kontor_adm_lpm

      _.findKey(vg3_kontor_adm, function(lpm) {
          lpm.checked = false
      });

       //clear vg3 salgsfaget
      let vg3_salgsfaget = this.$store.state.vg3_salgsfaget_lpm

      _.findKey(vg3_salgsfaget, function(lpm) {
          lpm.checked = false
      });

      //clear vg3 salgsfaget
      let vg3_sikkerhet = this.$store.state.vg3_sikkerhetsfaget_lpm

      _.findKey(vg3_sikkerhet, function(lpm) {
          lpm.checked = false
      });

      this.$router.push('/YFF')
    }
    },
    computed:{
        valgteLpms () {
            return this.$store.getters.getValgteLPM
        },
        fornavn: {
            //her må fornavn og etternavn også byttes om, når vi får skikkelig csv-fil
            get () { return this.$store.getters.get_valgtElev.Etternavn },
            set (fornavn) { this.$store.commit('setFornavn', fornavn) }
        },
        etternavn: {
            get () { return this.$store.getters.get_valgtElev.Fornavn },
            set (etternavn) { this.$store.commit('setEtternavn', etternavn)}
        },
        klasse: {
            get () { return this.$store.getters.get_valgtElev.Klasse },
            set (klasse) { this.$store.commit('setKlasse', klasse)}
        },
        utdanningsprog: {
            get () { return this.$store.getters.get_valgtElev.Utdanningsprogram },
            set (utdanningsprogram) { this.$store.commit('setUtdanningsprogram', utdanningsprogram) }
        },
        year: {
            get () { return this.$store.getters.get_valgtElev.Skoleår },
            set (year) { this.$store.commit('setYear', year)}
        },
        teacher: {
            get () { return this.$store.getters.get_valgtElev.Faglærer },
            set (teacher) { this.$store.commit('setTeacher', teacher)}
        },
        skole: {
            get () { return this.$store.getters.get_valgtElev.Skole },
            set (skole) { this.$store.commit('setSkole', skole)}
        },
        utskriftsdato: {
            get () { return this.$store.getters.get_valgtElev.Utskriftsdato },
            set (dato) { this.$store.commit('setDato'), dato }
        },
        prosjektNr: {
            get () { return this.$store.getters.get_valgtElev.Prosjekter[0].prosjektNr },
            set (projektNr) { this.$store.commit('setProjektNr', projektNr) }
        },
        laerefag: {
            get () { return this.$store.getters.get_valgtElev.Prosjekter[0].laerefag },
            set (laerefag) { this.$store.commit('setProjektLaerefag', laerefag) }
        },
        organisering: {
            get () { return this.$store.getters.get_valgtElev.Prosjekter[0].organisering },
            set (org) { this.$store.commit('setProsjektOrganisering', org) }
        },
        yearlyHours: {
            get () { return this.$store.getters.get_valgtElev.Prosjekter[0].yearlyHours },
            set (hours) { this.$store.commit('setProsjektYearlyHours', hours) }
        }
    },
    components: {
        appLpmValgliste:LpmValgliste,
        appProsjekter:Prosjekter
    },
    beforeCreate() {
      if (this.$store.getters.get_klasse.length < 1)
      this.$router.push('/')
    }
}
</script>

<style scoped>

  .yff-container{
    width:93%;
    font-family:'Ubuntu', sans-serif;
    text-align: center;
    margin-left:auto;
    margin-right:auto;
    margin-top:20px;
    margin-bottom:20px;
    padding:20px;
    padding-bottom:35px;
    background-color: #078893;
    color:white;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 6px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 6px -1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 6px -1px rgba(0,0,0,0.75);
  }

  .yff-heading{
    font-size: 2rem;
    font-family:'Ubuntu', sans-serif;
    margin-bottom:40px;
    color:#282827;
  }

  .godkjenn-input{
    display:block;
    width:35%;
    margin:5px auto 20px;
    text-align: center;
    border-radius:5px;
    padding:6px;
    font-size:1rem;
    font-family:'Ubuntu', sans-serif;
  }

  .lpm-list-item input {
    vertical-align: middle;
  }

  .lpm-text{
    vertical-align: middle;
  }

  .lpm-list-item{
    display:inline-block;
    border:1px solid white;
    border-radius:5px;
    width:85%;
    padding:7px;
    padding-left:50px;
    margin-left:auto;
    margin-right:auto;
    margin-top:10px;
    cursor:pointer;
    text-align: start;
    background-color: rgba(0,128,0 ,.7);
  }

  .lpm-list-item:hover {
    background-color: rgba(236, 15, 15, 0.7);
  }

  .lpm-list-container {
      margin-top:75px;
      margin-bottom:30px;
  }

  .dark-heading {
      color:black;
      cursor:pointer;
  }

  .link-btn {
    width: 100px;
    height: 40px;
    margin: 10px;
    margin-top: 40px;
    border-radius: 5px;
    border: 1px solid darkslategrey;
    font-weight: bold;
    color: white;
    background-color: #E37222;
  }

  .link-btn-big {
    width: 150px;
    height: 40px;
    margin: 10px;
    margin-top: 40px;
    border-radius: 5px;
    border: 1px solid darkslategrey;
    font-weight: bold;
    color: white;
    background-color: #E37222;
  }

  .link-btn:hover .link-btn-big:hover{
    background-color: #e88640;
    color: black;
  }

  .link-btn:active .link-btn-big:active{
    background-color: #a85f2b;
    color: white;
  }

</style>
